// routes.js
import Home from "./pages/Home";
import Professors from "./pages/Professors";
import Professor from "./pages/Professor";
import Assignatures from "./pages/Assignatures";
import Assignatura from "./pages/Assignatura";
import Materials from "./pages/Materials";
import Material from "./pages/Material";
import AccesObert from "./pages/AccesObert";
import TreballsFinals from "./pages/TreballsFinals";
import Organitzacions from "./pages/Organitzacions";
import Organitzacio from "./pages/Organitzacio";
import Titulacions from "./pages/Titulacions";
import Titulacio from "./pages/Titulacio";
import Video from "./pages/Video";
import About from "./pages/About";
import Contact from "@/pages/Contact";
import Admin from "@/pages/Admin";
import Cercar from "./pages/Cercar";
import Publish from "./pages/Publish";

const routes = [
    {path: "/", redirect: `/ca`},

    { path: '/:locale', component: Home, name: 'home' },

    { path: '/:locale/cerca', component: Cercar, name: 'cercar', query:'q' },

    { path: '/:locale/professorat', component: Professors, name: 'professors'},
    { path: '/:locale/professorat/cerca', component: Professors, name: 'professorsCerca', query:'q' },
    { path: '/:locale/professorat/:id', component: Professor, name: 'professor', meta: [{name: 'description', content: 'blabala'},{title: 'Professorat :id'}] },

    { path: '/:locale/organitzacions', component: Organitzacions, name: 'organitzacions' },
    { path: '/:locale/organitzacions/cerca', component: Organitzacions, name: 'organitzacionsCerca', query:'q' },
    { path: '/:locale/organitzacions/:tipus/:id', component: Organitzacio, name: 'organitzacio' },

    { path: '/:locale/assignatures', component: Assignatures, name: 'assignatures' },
    { path: '/:locale/assignatures/cerca', component: Assignatures, name: 'assignaturesCerca', query:'q' },
    { path: '/:locale/assignatures/:id', component: Assignatura, name: 'assignatura' },

    { path: '/:locale/titulacions', component: Titulacions, name: 'titulacions' },
    { path: '/:locale/titulacions/cerca', component: Titulacions, name: 'titulacionsCerca', query:'q' },
    { path: '/:locale/titulacions/:id', component: Titulacio, name: 'titulacio' },

    { path: '/:locale/materials', component: Materials, name: 'materials' },
    { path: '/:locale/materials/cerca', component: Materials, name: 'materialsCerca', query:'q' },
    { path: '/:locale/materials/:id', component: Material, name: 'material' },

    { path: '/:locale/accesObert', component: AccesObert, name: 'accesObert' },
    { path: '/:locale/accesObert/cerca', component: AccesObert, name: 'accesObertCerca', query:'q' },

    { path: '/:locale/treballsFinals', component: TreballsFinals, name: 'treballsFinals' },
    { path: '/:locale/treballsFinals/cerca', component: TreballsFinals, name: 'treballsFinalsCerca', query:'q' },

    { path: '/:locale/videos', component: Video, name: 'videos' },
    { path: '/:locale/videos/cerca', component: Video, name: 'videosCerca', query:'q' },

    { path: '/:locale/about', component: About, name: 'about' },

    { path: '/:locale/contact', component: Contact, name: 'contact' },

    { path: '/:locale/admin', component: Admin, name: 'admin' },

    { path: '/:locale/publish', component: Publish, name: 'publish' },


    //{ path: '/login', component: _import ( 'common / login'), name: 'login', meta: {title: 'login'}},


    //{ path: '/materials/:id', component: Material, name: 'material' },
    { path: '*', name: 'error', component: Home },
];


export default routes;
