<script>

    import axios from 'axios'
    import Facetes from '../components/Facetes.vue'
    import Pagination from "../components/Pagination"
    import PaginationHeader from "../components/PaginationHeader"
    import ChartByTipus from "../components/ChartByTipus"
    import ChartByData from "../components/ChartByData"
    import Utils from "../extends/Utils"
    import AddThis from 'vue-simple-addthis-share'

    export default {
        mixins: [Utils],
        name: 'Assignatura',
        components: {
            PaginationHeader,
            Facetes,
            ChartByTipus,
            ChartByData,
            Pagination,
            AddThis
        },
        data() {
            return {
                assignatura: [],
                materials: [],
                facetes: [],
                facetesPivot: [],
                professors: [],

                navigation_start: 0,
                navigation_end: 0,
                navigation_length: 20,
                navigation_total: 0,
                totalPages: 1,
                currentPage: 1,
                query: "material_assignatures:"+this.$route.params.id,
                sort: "data desc, material_data_available desc",
                errors: []
            }
        },


        methods: {
            setMetaTitle(elem){
              if(this.$language.current == 'ca') document.title = "Aprèn - " + elem.assignatura_nom_ca
              else if(this.$language.current == 'es') document.title = "Aprèn - " + elem.assignatura_nom_es
              else document.title = "Aprèn - " + elem.assignatura_nom_en
            },
            getTipus(tipus, index) {
              //paso la variable tipus a String
              let tipusStr = tipus+""
              return tipusStr.split(",")[index];
            },
            hideShow(index){
                var x = document.getElementById(index)
                if (x.style.display === "none") {
                    x.style.display = "block";
                } else {
                    x.style.display = "none";
                }
            },
            solrQueryProfessorsAssignatura: function (codi) {
              axios.get(this.API_URL + '/solrClient/execute?query=q=professor_assignatures:'+codi+'??fl=id,professor_nom,professor_num_materials??rows=9999999??sort=professor_nom_sort asc')
                  .then(response => {
                    this.professors = response.data.response.docs;
                  })
                  .catch(e => {
                    this.errors.push(e)
                  })
            },
            solrQueryProfessor: function (gaussId) {
                axios.get(this.API_URL + '/solrClient/execute?query=q=id:'+gaussId)
                    .then(response => {
                        if(response.data.response.numFound == 0) window.location = this.APREN_URL
                        this.assignatura = response.data.response.docs[0];
                        this.setMetaTitle(this.assignatura)
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            },
            onChange(event) {
                this.solrQuery(1, this.navigation_length, this.query, event.target.value)
            },
            solrQuery: function (currentPage, length, query, sort) {

                this.currentPage = currentPage;
                this.query = query;
                this.sort = sort;

                let start = (currentPage-1)*length

                axios.get(this.API_URL + '/solrClient/execute?query=q='+query+'??facet.field=subtipus??facet.field=data??facet.field=material_rights??facet.field=material_arees_tematiques??facet.field=material_idiomes??facet.field=material_examen_resolt??facet.field=material_professors_faceta??facet.field=material_centres??facet.field=material_departaments??facet.field=material_campus??facet.field=material_titulacions_actives??facet.field=material_titulacions_no_actives??facet.pivot=subtipus,data??facet=on??f.data.facet.sort=index??f.material_ods.facet.sort=index??facet.mincount=1??rows='+length+'??start='+start+'??sort='+sort)
                //axios.get(this.API_URL + '/solrClient/execute?query=q='+query+'??facet.field=subtipus??facet.field=data??facet.field=material_idiomes??facet.field=material_arees_tematiques??facet.field=material_examen_resolt??facet.field=material_professors_faceta??facet.field=material_centres??facet.field=material_departaments??facet.field=material_campus??facet.field=material_titulacions??facet.field=material_assignatures_faceta??facet.pivot=subtipus,data??facet=on??f.data.facet.sort=index??facet.mincount=1??rows='+length+'??start='+start+'??sort='+sort)
                    .then(response => {

                        this.materials = response.data.response.docs;
                        this.facetes = response.data.facet_counts.facet_fields;
                        this.facetesPivot = response.data.facet_counts.facet_pivot;

                        //reverse facet data
                        response.data.facet_counts.facet_fields.data = this.reverseFacetData(response.data.facet_counts.facet_fields.data)

                        this.navigation_start = response.data.response.start+1;
                        this.navigation_total = response.data.response.numFound;
                        this.navigation_length = length;
                        this.navigation_end = response.data.response.start+this.navigation_length >= response.data.response.numFound ? response.data.response.numFound : response.data.response.start+this.navigation_length

                        this.totalPages = Math.ceil(response.data.response.numFound / length);

                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
            }
        },

        created() {
            this.solrQueryProfessor(this.$route.params.id)
            this.solrQuery(1, this.navigation_length, this.query, this.sort)
            this.solrQueryProfessorsAssignatura(this.$route.params.id)
        }
    }

</script>

